    export default {
        data() {
            return {
                info: '',
                lateTime: null,  // 迟到总时间
                earlyTime: null,  // 早退总时间
                addsTime: null,  // 加班总时间
                turnday: false,  //  出勤天数显示隐藏切换
                turnclasses: false,  //  出勤班次显示隐藏切换
                restday: false,  //  休息天数显示隐藏切换
                late: false,  //  迟到显示隐藏切换
                leave: false,  //  早退显示隐藏切换
                work: false,  //  外勤显示隐藏切换
                lackCark: false,  //  缺卡显示隐藏切换
                addTime: false,  //  加班显示隐藏切换
                absenteeism: false,  //  旷工显示隐藏切换
//                个人信息
                meList: {
                    head: require("@/assets/images/head.jpg"),
                    name: "陈小丽",
                    post:"服务部: 员工",
                    time: "2018.9.6"
                },
            }
        },
        mounted(){
            this.getInfo();   // 初始页页面数据
        },
        methods:{
            getInfo(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.turn,
                    type: "post",
                    data: {

                    },
                    success: (response, allResponse) => {
                        console.log('response',response);
                        this.info = response;
                        this.meList = response.user
                        response.work_info.workLate.forEach((item,index)=>{
                            this.lateTime += item.time;
                        });
                        response.work_info.workEarly.forEach((item,index)=>{
                            this.earlyTime += item.time;
                        });
                        response.work_info.workOver.forEach((item,index)=>{
                            this.addsTime += item.time;
                        })
                    }
                });
            },
//            头部导航箭头按钮
            backHome(){
                // this.$router.push("/manage")
                this.$router.go(-1)
            },
//            点击查看月历
            month(){
                this.$router.push('/check/month');
            },
            //            选择时间按钮弹窗
            onConfirm () {
                console.log("我点击了确定按钮")
            },
        }
    }



